<script>
import '../components/login.less'
// import '../mock/index'
export default {
  data () {
    return {
      loginview: true,
      register: false,
      input: '',
      input3: '',
      user: '',
      password: '',
      phone: '',
      name: ''
      // bodyBgImage: 'url(' + require('../../assets/bg.jpg') + ')'
    }
  },
  methods: {
    gouwuche(){
      this.$router.push('/money')
    },
    shouye(){
      this.$router.push('/')
    },
    changestaus () {
      this.loginview = !this.loginview
      this.register = !this.register
    },
    login () {
      
      var _this = this
      if(_this.input3 = ''){
        alert('')
      }else{
      this.$http.post('/api/user/user', {
            input3: this.input3,
            input: this.input
            })
            .then(function (response) {
             if(response.staus = 200){
                alert('')
                var id = 1;
                _this.$router.push({
                    name: 'Main',//undefined
                    path: '/',
                    params: {
                      Id: _this.user
                    }
                })
              }
            })
            .catch(function (error) {
              console.log(error);
            });
          }
      // this.$router.replace('/main')
    },
    registers () {
      var _this = this
      var regEmail = /^[A-Za-z0-9\u4e00-\u9fa5]+@[a-zA-Z0-9_-]+(\.[a-zA-Z0-9_-]+)+$/
      if (_this.user != '' && !regEmail.test(_this.user)) {
          alert('')
          _this.user = ''
      }else{
      this.$http.post('/api/user/user', {
            user: this.user,
            password: this.password,
            phone: this.phone,
            name: this.name
            })
            .then(function (data) {
              // console.log(data)
              if(data.staus = 200){
                alert('')
                _this.changestaus()
              }
            }).catch(function (error) {
              console.log(error);
            });
            }
    }
  }
